import React,{useState} from 'react'

import "../../styles/ventas/ventasModal.css";

import { fetchConTokenJson } from '../../helpers/fetch';

export const ModalVentas = ({productoId,colorId,data,cerrarModal}) => {
    
    
    const [idProductoVenta, setIdProductoVenta] = useState([productoId])
    //Vrios colores
    const [idColoresVenta, setIdColoresVenta] = useState([colorId])
    //Varios tallas[]
    const [idTallas, setIdTallas] = useState([])

    //varios stock []
    const [numeroVentas, setnumeroVentas] = useState([])

    //ENVIAR VENTA
    const enviarVenta = async(e)=>{
        e.preventDefault();
        const token = localStorage.getItem("token")
      

        const data={
            idProductoVenta,
            idColoresVenta,
            idTallas,
            numeroVentas

        }
        console.log("=>>>>>=",data)

        const resp=  await fetchConTokenJson("new-venta",data,"POST")

        const body = await resp.body;

        if(body.ok){
            console.log("Venta hecha")
        }else{
            console.log("error")
        }
    }

    const handleTallasVentaChange =(e,idTalla,posicion)=>{

        let auxTallas = idTallas;

        if (e.target.checked){
            auxTallas[posicion]=idTalla;
        }
        else{
            auxTallas=auxTallas.filter(i=>{
                return (i!==idTalla)
            })
        }
        
        setIdTallas(auxTallas)
    
    }

    const handleStockVentaChange = (e,posicion)=>{
        let auxStock= numeroVentas;
        auxStock[posicion]=e.target.value;

        setnumeroVentas(auxStock)
    }
    


    

    return (
        <div className="modal_ventas mb-5 mt-2">
            <button onClick={()=>cerrarModal()} className="btn btn-danger">X</button>
            <h5 className="text-center pb-0">Cuanto vendiste hoy ?</h5>
            
            <table className="mt-1">
                <tr>
                    <td>
                        Talla:
                    </td>
                    <td>
                        Stock:
                    </td>
                </tr>
                    

                    {
                        data.tallas.map((talla,index)=>{
                            return(
                                

                                
                                <tr>
                                    <td>
                                        {talla.nombreTalla}
                                        <input 
                                        onChange={(e)=>handleTallasVentaChange(e,talla._id,index)} 
                                        type="checkbox"
                                         value={talla.nombreTalla} 
                                         /> 
                                    </td>            
                                    <td>
                                       <input 
                                       onChange={(e)=>handleStockVentaChange(e,index)} 
                                       type="number"
                                       placeholder={talla.stock}
                                       />
                                    </td>
                                </tr>
                               
                                )
                            })
                        }
                    <button onClick={enviarVenta} 
                    className="btn btn-primary mt-3 text-center">
                        Guardar Venta
                    </button>

            </table>
            
        </div>
    )
}

