import React, { useEffect, useState } from 'react';


import axios from "axios";
import dotEnv from "dotenv";
dotEnv.config()


const apiG= axios.create();

export const ProductoScreen =  (props) => {
    
    const {match:{params}} = props;

    const [productoMostrar, setProductoMostrar] = useState([])

    const productoId = params.producto;
    
    const portPeticiones = process.env.PORTPETICIONES || "http://localhost:4000/"

    const token = localStorage.getItem("token")

    const headers = {
        "x-token":token
    }
    
    useEffect(() => {
        axios.post(`${portPeticiones}admin/api/ver`,
        {
            
            productoId
        },
        {
            headers
        }
    
        ).then(res=>{
            setProductoMostrar([res.data.productoSolo])

        })
        .catch(e=>console.log(e))
    }, [productoId])

    console.log(productoMostrar)
    
    return (
        <div className="container mt-4">
           {
               productoMostrar.map(i=>{
                   return(
                    
                    <div className="row">
                        <div className="col-5">

                            <h4 className="text-center">{i.nombre}</h4>
                            <hr/>
                            <div className="row mt-3">

                            <div className="col-5">
                                <p>Precio x Mayor: {i.precioMayor}</p>
                                <p>Precio: {i.precio}</p>
                            </div>
                            <div className="col-7">
                                DESCRIPCION: 
                                <br/>
                                {i.descripcion}
                            
                            </div>
                            </div>
                            <div className="row">
                                <div className="col-6">
                                    Categoria General: {i.categoriaGeneral}
                                </div>
                                <div className="col-6">
                                    Categoria: {i.categoria[0]}

                                </div>

                            </div>

                            <div className="row">
                                <div>
                                    Stock Total : {i.stockTotal}
                                </div>
                            </div>
                            <div className="row">
                                <div>
                                    Materiales :<br/> 
                                    {
                                        i.materiales.map(material=>{
                                            return (
                                                <p>{material}</p>
                                            )
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                        
                        <div className="col-7"> 
                            {
                                i.colores.map(e=>{
                                    return (
                                        <div className="row mt-4">
                                            
                                            <div className="col-4">
                                                <img src={e.urlImagenes[0]} width="100%" height="100%"/>
                                            </div>

                                            <div className="col-8">
                                                <div>
                                                    Genero: {e.genero}
                                                </div>

                                                <div>
                                                    {
                                                        e.tallas.map(talla=>{
                                                            return (
                                                                <div className="text-center m-2">
                                                                    Talla: {talla.nombreTalla}
                                                                    <br/>
                                                                    Stock: {talla.stock}
                                                                    <hr/>
                                                                </div>
                                                            )
                                                        })
                                                    }
                                                </div>

                                            </div>


                                        </div>
                                    )
                                })
                            }
                        
                        </div>
                    </div>
                        
                        
                   )
               })

           }
        </div>
    )
}
