import React,{useState,useEffect} from 'react';
import axios from "axios";
import { ModalVentas } from './ModalVentas';

import dotEnv from "dotenv";
dotEnv.config()


export const VerVentas = (props) => {
    

    const {match:{params}} = props;

    const [productoMostrar, setProductoMostrar] = useState([])

    const productoId = params.producto;
    
    const portPeticiones = process.env.PORTPETICIONES || "http://localhost:4000/"

    const token = localStorage.getItem("token")

    const headers = {
        "x-token":token
    }
    
    
    axios.post(`${portPeticiones}admin/api/ver`,
    {
        
        productoId
    },
    {
        headers
    }

    ).then(res=>{
        setProductoMostrar([res.data.productoSolo])

    })
    .catch(e=>console.log(e))
    

  

    const [openModal, setOpenModal] = useState(false);

    const [auxliar, setAuxliar] = useState(null);

    const abrirModal = (colorId)=>{
           
        setAuxliar(colorId)
        
        setOpenModal(true);
        
        
    }

    const cerrarModal = ()=>{
        
        setOpenModal(false)
        
        
    }
    

    return (
        <div className="container">
            {productoMostrar.map(i=>{
                return (
                    <div>

                    <h1>
                        {i.nombre}
                    
                    </h1>
                    
                    <hr/>

                    <div>
                        <div>
                            {i.descripcion}
                        </div>

                        <div>
                            S/{i.precioMayor}
                            <br/>
                            S/{i.precio}
                        </div>
                    </div>

                    <div className="row">
                        {
                            i.colores.map((color,index)=>{

                                return (
                                    <div className="col-3 mt-3">
                                        <img style={{
                                            cursor:"pointer"
                                            
                                        }} onClick={()=>abrirModal(color._id)} src={color.urlImagenes[0]} className="img-fluid pointer"/>
                                        
                                        <table className="table table-bordered">
                                            <tr>

                                                <td>
                                                    Talla:
                                                </td>
                                                <td>
                                                    Stock:
                                                </td>
                                            </tr>
                                        
                                        {
                                            color.tallas.map((talla,index)=>{
                                                return (
                                                    <tr>
                                                        <td>    
                                                            {talla.nombreTalla}
                                                        </td>
                                                        <td>
                                                            {talla.stock}
                                                        </td>
                                                    </tr>
                                                )
                                            })
                                        }
                                        </table>
                                        {(auxliar===color._id && openModal===true)?<ModalVentas
                                         productoId={productoId}
                                         colorId={color._id}  
                                         cerrarModal={cerrarModal} 
                                         data={color}/>
                                         
                                         :<></>}
                                    </div>
                                )
                            })
                        }
                    </div>

                    </div>
                )
            })}
        </div>
    )
}