import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,

} from "react-router-dom";
import { CrearProducto } from "../components/actions/CrearProducto";
import { ProductoScreen, productoScreen } from "../components/productos/productoScreen";



import { ProductScreen } from "../components/productos/ProductScreen";
import { UserScreen } from "../components/usuarios/UserScreen";
import { ResumenVentas } from "../components/ventas/ResumenVentas";
import { VentasScreen } from "../components/ventas/VentasScreen";
import { VerVentas } from "../components/ventas/VerVentas";



export const AdminRoutes=() =>{
  return (
    <Router>
    
        <Switch>
          
          

          <Route exact path="/admin-api/users" component={UserScreen}/>
            
          <Route exact path="/admin-api/productos" component={ProductScreen}/>

          <Route exact path="/admin-api/productos/ver/:producto" component={ProductoScreen} />

          <Route exact path="/admin-api/productos/crear-producto" component={CrearProducto} />

          <Route exact path="/admin-api/ventas" component={VentasScreen} />

          <Route exact path="/admin-api/ventas/:producto" component={VerVentas} />
          
          <Route exact path="/admin-api/ventas/resumen/:fecha" component={ResumenVentas} />

          <Redirect to="/admin-api/users"/>

        </Switch>
      
    </Router>
  );
}
