import React,{useState} from 'react';
import axios from "axios";

import dotEnv from "dotenv";
dotEnv.config()

const ventasVerApi=axios.create();
export const ResumenVentas = () => {
    


    const portPeticiones = process.env.PORTPETICIONES || "http://localhost:4000/"



    const token = localStorage.getItem("token")
    
    const [ventasVer, setVentasVer] = useState([])

    ventasVerApi.get(`${portPeticiones}admin/api/ver-ventas`,{
        headers:{
            
            
            "x-token":token
        }
    }
    ).then((res)=>{
        setVentasVer(res.data.ventas)

    }
    )

    

    return (
        <div className="container">
            <h1>Resumen Ventas</h1>
            <hr/>
            <div className="mt-3">
                {
                    ventasVer.map(venta=>{
                        return (
                            <div className="row">
                                <div className="col-8">
                                    <p>Fecha venta : {venta.fechaVenta}</p>
                                    
                                    <p>Cantidad vendida :{venta.cantidadVendida}</p>
                                    
                                    <p>Id color vendido : {venta.colorProductoVendido._id}</p>
                                    <p>Id talla vendida : {venta.tallaProductoVendido._id}</p>
                                </div>
                                <div className="col-4">
                                    <img 
                                    className="img-fluid"
                                    src={venta.colorProductoVendido.urlImagenes[0]}
                                     />
                                </div>
                                <hr/>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    )
}
